import React from 'react'
import { Container,Row } from 'react-bootstrap'
import "./MainTitle.css"

const MainTitle = ({title,children}) => {
  return (
    <div className="maintitle">
        <Container>
            <Row>
                <div className="page">
                    {title && (
                        <>
                        <h1 className="heading">{title}</h1>
                        <hr />
                        </>
                    )}
                    {children}
                </div>
            </Row>
        </Container>
    </div>
  )
}

export default MainTitle